import { useNavigate } from 'react-router-dom'
import { useEffect } from 'react'
import Btn from '../../elements/Btn'
import styled from 'styled-components'
import { DAY } from '../../utils/constant'

export default function UserInfoAdmin({ info, setDisable, show, setShow }) {
  const nav = useNavigate()
  const classDate = (classData) => {
    const classDate = classData.split('/')[1]
    const year = `${classDate[0]}${classDate[1]}${classDate[2]}${classDate[3]}`
    const month = `${classDate[4]}${classDate[5]}`
    const date = `${classDate[6]}${classDate[7]}`
    const day = DAY[Number(`${classDate[8]}`)]
    return `${year}${month}${date}${day}`
  }
  const classTime = (classData) => {
    const classDate = classData.split('/')[2]
    const hour = `${classDate[0]}${classDate[1]}`
    const minute = `${classDate[2]}${classDate[3]}`
    return `${hour}:${minute}`
  }
  useEffect(() => {
    setDisable(false)
  }, [setDisable])
  const handleClick = () => {
    nav('/main/useradmin/alterclass')
  }
  return (
    <div>
      {info.name !== '' ? (
        <>
          <InfoUl>
            <InfoLi>
              <Menu> :</Menu>
              {info.name}
            </InfoLi>
            <InfoLi>
              <Menu> :</Menu>
              {info.birth}
            </InfoLi>
            <InfoLi>
              <Menu> :</Menu>
              {info.belong}
            </InfoLi>
            {info.isClass ? (
              <>
                <InfoLi>
                  <Menu> :</Menu>
                  {info.class.split('/')[0]}
                </InfoLi>
                <InfoLi>
                  <Menu> :</Menu>
                  {classDate(info.class)}
                </InfoLi>
                <InfoLi>
                  <Menu> :</Menu>
                  {classTime(info.class)}
                </InfoLi>
                <InfoLi>
                  <Menu> :</Menu>
                  {info.signInTime}
                </InfoLi>
              </>
            ) : (
              <InfoLi>  .</InfoLi>
            )}
            <Btn text='  ' onClick={handleClick} type='button' />
          </InfoUl>
        </>
      ) : (
        show && <div>    </div>
      )}
    </div>
  )
}

const InfoUl = styled.ul`
  width: 300px;
  font-size: 0.95rem;
`
const InfoLi = styled.li`
  margin-bottom: 10px;
`
const Menu = styled.span`
  margin-right: 10px;
  font-weight: 600;
  font-size: 1rem;
`
