import { useState, useEffect } from 'react'
import { addUserData, signInUserData } from '../firebase/firebase'
import InputBox from '../elements/InputBox'
import PhoneInput from '../elements/PhoneInput'
import Select from '../elements/Select'
import Btn from '../elements/Btn'
import { useNavigate, useSearchParams } from 'react-router-dom'
import BackBtn from '../elements/BackBtn'
import Page from '../elements/Page'
import Form from '../elements/Form'
import Header from '../elements/Header'
import { belongArr } from '../store/Data'
import PageLogo from '../elements/PageLogo'
import styled from 'styled-components'
import { res } from '../firebase/api'
import Waiting from '../elements/Waiting'
import Footer from '../elements/Footer'
import SignInInput from '../elements/SignInInput'

export default function Signin() {
  const today = new Date()
  const userInfo = {
    name: '',
    birth: '',
    belong: '',
    phoneNumber: '',
    class: '',
    id: `${today.getFullYear()}${
      Number(today.getMonth()) + 1
    }${today.getDate()}${today.getHours()}${today.getMinutes()}${today.getSeconds()}${today.getMilliseconds()}`,
    agree: 'false',
  }
  const [info, setInfo] = useState(userInfo)
  const [check, setCheck] = useState(true)
  const [isWaiting, setIsWaiting] = useState(false)
  const nav = useNavigate()
  const [searchParams] = useSearchParams()
  const [success, setSuccess] = useState(false)
  useEffect(() => {
    const imp_uid = searchParams.get('imp_uid')
    if (imp_uid) {
      setIsWaiting(true)
      res(imp_uid, setSuccess, setInfo, setIsWaiting, nav)
    }
  }, [nav, searchParams])
  const handleBelongChange = (e) => {
    const targetName = e.currentTarget.name
    const targetValue = e.currentTarget.value
    setInfo((prev) => ({ ...prev, [targetName]: targetValue }))
  }
  const handleCheckClick = (e) => {
    const isAgree = e.currentTarget.dataset.ischeck
    setCheck((prev) => !prev)
    setInfo((prev) => ({
      ...prev,
      agree: isAgree,
    }))
  }
  const handleCertificationClick = () => {
    if (info.phoneNumber.length >= 10) {
      setIsWaiting(true)
      signInUserData(info, nav, setIsWaiting)
    } else {
      if (info.phoneNumber.length < 10) {
        alert('    .')
      } else {
        alert('  .')
      }
    }
  }
  const handlePhoneInputChange = (num) => {
    setInfo((prev) => ({
      ...prev,
      phoneNumber: num,
    }))
  }

  const handleSubmitClick = (e) => {
    e.preventDefault()
    if (
      info.belong !== '' &&
      info.name !== '' &&
      info.agree === 'true' &&
      info.birth !== '' &&
      info.phoneNumber.length >= 10
    ) {
      setIsWaiting(true)
      addUserData('user', info, nav, setIsWaiting)
    } else {
      if (info.agree !== 'true') {
        alert('   .')
      } else if (info.belong === '') {
        alert('  .')
      } else {
        alert('  .')
      }
    }
  }

  return (
    <Page>
      <Header>
        <BackBtn top='0px' left='0px' />
      </Header>
      <PageLogo
        height='170px'
        text='   .'
        text2={success ? '   .' : ' ,  .'}
      />
      {isWaiting ? (
        <Waiting />
      ) : (
        <SignInBox>
          <Form action='submit'>
            {success ? (
              <>
                <AgreeBox>
                  <p className='bold'> () :</p>
                  <p>        .</p>
                  <p className='small'>  :   </p>
                  <p className='small'>  : , , , </p>
                  <p className='small'> :   </p>
                  <p>    . ,         .</p>
                </AgreeBox>
                <AgreeCheckBox>
                  <input
                    type='checkbox'
                    name='agree'
                    data-ischeck={check}
                    onClick={handleCheckClick}
                    style={{ marginTop: '12px' }}
                  />
                  <label style={{ fontSize: '13px', marginTop: '10px' }}>   .</label>
                </AgreeCheckBox>
                <InputBox name='name' label='' src={info.name !== '' ? true : false} alt='CHECKIMAGE'>
                  <SignInInput name='name' value={info.name} width='140px' disabled={true} />
                </InputBox>
                <InputBox name='birth' label='' src={info.birth !== '' ? true : false} alt='CHECKIMAGE'>
                  <SignInInput name='birth' value={info.birth} width='140px' disabled={true} maxLength='6' />
                </InputBox>
                <InputBox
                  name='phoneNumber'
                  label=' '
                  src={info.birth !== '' ? true : false}
                  alt='CHECKIMAGE'
                >
                  <SignInInput name='birth' value={info.phoneNumber} width='140px' disabled={true} />
                </InputBox>
                <InputBox name='belong' label='' src={info.belong !== '' ? true : false} alt='CHECKIMAGE'>
                  <Select name='belong' onChange={handleBelongChange} selectArr={belongArr} width='auto'></Select>
                </InputBox>
                <Btn text=' ' type='submit' onClick={handleSubmitClick} />
              </>
            ) : (
              <>
                <InputBox name='phoneNumberInput' label='' alt='CHECKIMAGE'>
                  <PhoneInput name='phoneNumber' setState={handlePhoneInputChange} />
                </InputBox>

                <Btn text='' type='button' onClick={handleCertificationClick} />
              </>
            )}
          </Form>
        </SignInBox>
      )}
      <Footer />
    </Page>
  )
}

const AgreeBox = styled.div`
  font-size: 14px;
  padding: 5px;
  width: 300px;
  height: 60px;
  border: 3px solid #e6e6e6;
  border-radius: 5px;
  overflow: scroll;
  p {
    margin: 2px 0;
  }
  .bold {
    font-weight: 700;
  }
  .small {
    font-size: 12px;
  }
`

const AgreeCheckBox = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 220px;
`
const SignInBox = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 10px;
  padding: 10px 0;
  font-family: 'Noto Sans KR', sans-serif;
`
